<!-- eslint-disable vue/multi-word-component-names -->
<script setup>
import { ref } from 'vue'
const translateUrl = ref([
  { name: '有道', url: 'https://fanyi.youdao.com/index.html?keyfrom=baidu2#/' },
  {
    name: '百度',
    url: 'https://fanyi.baidu.com/mtpe-individual/multimodal?aldtype=16047&ext_channel=Aldtype#/auto/zh'
  }
])
const openTranslate = (url) => {
  window.open(url)
}
</script>
<template>
  <div class="container">
    <div class="choice" v-for="translate in translateUrl" :key="translate">
      <el-divider border-style="double" />
      <div class="goto" @click="openTranslate(translate.url)">{{ translate.name }}</div>
    </div>
  </div>
</template>
<style scoped>
.container {
  width: 85%;
}
.goto {
  cursor: pointer;
  text-decoration: underline;
  width: 2em;
}
</style>
